:root {
  --black-color: #000;
  --cyan-color: #00acdc;
}

html, body, div, header, ul, li, a, img {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.header__logo img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: var(--black-color);
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--cyan-color);
}

.is-active:after {
  content: "";
  background-color: var(--cyan-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
